import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import PropTypes from 'prop-types';

import { Table, Icon, Divider, Tooltip,Form,Row} from 'antd';

import EditableFormCell from "./EditableCell.js";

import "./Table.less";

class EditableTable extends Component {
    state = {
        editId: 0,
        pagination: {
            showSizeChanger: true
        },
        sorter: {
            order:"asc"
        }
    }
    componentWillMount() {

    }
	getColums = (columns,addColumns) => {
        if (addColumns = []) {
            const { editId, sorter,isEditable } = this.state;
            var { getActionsShow } = this.props;

            getActionsShow = getActionsShow ? getActionsShow : (() => []);

            columns = columns.map(c => c.dataIndex === sorter.field ? ({ ...c, sortOrder: sorter.order }) : ({ ...c, sortOrder: false }));

			var arr = [];
			if(isEditable){
				arr = [{
						title: 'Действия',
						dataIndex: 'actions',
						render: (name, record) => {
							const { editId } = this.state;
							let { keyId } = this.props;
							let key = keyId || "id";
							var actions =   [
												<Icon type="edit" onClick={() => this.editable(record[key])} />,
												...getActionsShow(record)
											]

							if (record[key] !== editId) {
								var arr = [];
								actions.forEach((action,i) => {
									arr.push(action);
									if (i != actions.length - 1)
										arr.push(<Divider type="vertical" />);
								});
								return <Row>
											{arr.map((a, i) => ({ ...a, key: i }))}
										</Row>
							}
							return	<Row>
										<Tooltip placement="top" title="Отменить">
											<Icon type="close" onClick={() => this.editable(0)} />
										</Tooltip>
										<Divider type="vertical" />
										<Tooltip placement="top" title="Сохранить">
											<Icon type="check" onClick={() => this.save()} />
										</Tooltip>
									</Row>
						}
					}]
			}
			

            return [...columns, ...addColumns,...arr];
		}
		return columns;
	}
    editable = (editId) => {
        
        this.setState({ editId })
    }
	save = () => {
		const { editId } = this.state;
		const { form,onChange,users } = this.props;
        let { keyId } = this.props;
        let key = keyId || "id";
        
		form.validateFields((err, values) => {
			if (!err) {
                var user = users.find(u => u[key]==editId);
				onChange && onChange({...user,...values})
				this.editable(0);
			}
		});
    }
	onSave = (row) => {
		const {users,onChange} = this.props;
		const newData = users.map((u)=>{
			if(u.id===row.id){
				return row;
			}
			return u;
		});
		if(onChange){
			onChange(newData);
		}
	}

	render() {
        const { editId, sorter} = this.state;
        const { isEditable, pagination, onSortChange, columns, addColumns, users, form} = this.props;

        let { keyId } = this.props;
        let key = keyId || "id";
        
		const components = {
			body: {
				cell: EditableFormCell,
			},
		};
        const columns3 = this.getColums(columns, addColumns).map((col) => {
			if (!col.editable) {
				return col;
			}
			return {
				...col,
				onCell: user => ({
                    ...col,
                    user,
                    itemId: user[key],
					type: (col.type)?col.type:"text",
					editable: col.editable,
                    editing: user[key] == editId,
					dataIndex: col.dataIndex,
					title: col.title,
                    form: form,
					handleSave: (row)=>this.onSave(row),
				}),
			};
		});
        return (
			    <Table
                    key={key}
				    rowKey={user => {
                        return user[key]+""
				    }}
				    components={components}
				    rowClassName={() => 'editable-row'}
				    dataSource={users}
				    columns={columns3}
                    pagination={pagination}
                    onChange={(pagination, filters, sorter) => {
                        
                            this.setState({ sorter: { ...sorter} }, () => {
                            sorter.order = sorter.order || "";
                            onSortChange(pagination.current, 10, sorter.field, sorter.order.replace("ascend", "asc").replace("descend", "desc"));
 
                        })
                    }}
                />
		);
	}
}
EditableTable.propTypes = {
	addColumns:	PropTypes.arrayOf(
					PropTypes.shape({
					  title: PropTypes.string.isRequired,
					  dataIndex: PropTypes.string.isRequired,
					  key: PropTypes.string.isRequired,
					  type: PropTypes.string,
					  render: PropTypes.function,
					  editable: PropTypes.bool,
					})
				),
	isEditable:	PropTypes.bool
};
export default Form.create()(EditableTable);