import React from 'react';
import 'antd/dist/antd.css';
import Table from '../components/CRUDTable';

import PropTypes from 'prop-types';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader } from '@vkontakte/vkui';

const columns = [{
		title: "Номер",
		dataIndex: 'num',
	}, {
		title: "Название",
		dataIndex: 'name',
		
		editable: false,
	},{
		title: "Лайки",
		dataIndex: 'likes',
		sorter: true,
		editable: false,
	},{
		title: "Репосты",
		dataIndex: 'reposts',
		sorter: true,
		editable: false,
	}, {
		title: "Комментарии",
		dataIndex: 'comments',
		sorter: true,
		editable: false,
	}, {
		title: "Баллы",
		dataIndex: 'balls',
		sorter: true,
		editable: false,
}];
