import React from 'react';
import { Select, Checkbox, InputNumber, Input } from 'antd';

var { Component } = React;
const { Option } = Select;

class GetInput extends React.Component {
    static getDerivedStateFromProps(nextProps) {
        // Should be a controlled component.
        if ('value' in nextProps) {
            return {
                ...(nextProps.value || {}),
            };
        }
        return null;
    }
    constructor(props) {
        super(props);

    }

    handleCurrencyChange = currency => {
        if (!('value' in this.props)) {
            this.setState({ currency });
        }
        this.triggerChange({ currency });
    };

    triggerChange = changedValue => {
        // Should provide an event to pass value to Form.
        const onChange = this.props.onChange;
        if (onChange) {
            onChange(Object.assign({}, this.state, changedValue));
        }
    };

    render() {
        var { options, type, typeImage, ref, onChange, value, objId } = this.props;
        options = options ? options : [];
        if (type === 'bool') {
            return <Checkbox chacked={value} ref={ref} onChange={onChange} />;
        }
        if (type === 'number') {
            return <InputNumber value={value} ref={ref} onChange={onChange} />;
        }

        if (type === 'select') {
            if (options.length > 0)
                return <Select value={value} ref={ref} onChange={onChange}>
                    {
                        options.map(opt => <Option key={opt.value} value={opt.value}>{opt.label}</Option>)
                    }
                </Select>
            return <p>Нет обьектов для выбора</p>;
        }
        return <Input ref={ref} value={value} onChange={onChange} />;
    }
}
export default (GetInput);


