import React from 'react';
import { Select, Icon, Row, Modal, Form} from 'antd';
import Table from "../Table/";

import GetInput from '../GetInput/GetInput';

const FormItem = Form.Item;

class CRUDTable extends React.Component {
    state = {

    };
    constructor(props) {
        super(props);
    }
    render() {
        const { items, totalCount,isEditable, isPagination, keyId, columns, getItems,createItem,deleteItem, updateItem, form, titleModal, isOpenModal, closeModal} = this.props;
        const { getFieldDecorator } = form;
        var pagination = isPagination?{ ...this.state.pagination, total: totalCount }:false;
        return (
            <Row>
                <Modal
                    title={titleModal ? titleModal :"Добавить"}
                    visible={isOpenModal}
                    okText={"Ок"}
                    cancelText={"Отмена"}
                    onOk={() => {
                        form.validateFields((err, values) => {
                            if (!err) {
                                createItem(values);
                            }
                        });
                    }}
                    onCancel={() => { closeModal && closeModal() }}
                >
                    <Form>
                    {
                        columns.filter(c => c.editable).map((c,i) => {
                            return <Row key={i}>
                                        <FormItem
                                            label={c.title}
                                            style={{ margin: 0 }}
                                        >
                                        {
                                            getFieldDecorator(c.dataIndex, {
										        rules: []
                                            })(
                                                <GetInput type={c.type} options={c.options} typeImage={c.typeImage} objId={0}/>
									        )
                                        }
                                        </FormItem>
                                    </Row>
                        })
                    }
                    </Form>
                </Modal>
                <Table
                    key={keyId}
                    onChange={updateItem} 
                    isEditable={isEditable}
                    users={items}
                    keyId={keyId}
                    pagination={pagination}
                    getActionsShow={(item) => [<Icon type="delete" onClick={() => { deleteItem(item) }} />,]}
                    columns={columns}
                    onSortChange={getItems}
				/>
			</Row>
        );
    }
}
export default Form.create()(CRUDTable);
