import React,{Component} from 'react';

import {Input,Form,Checkbox,Select} from 'antd';

import PropTypes from 'prop-types';

import GetInput from '../GetInput/GetInput';


const FormItem = Form.Item;


class EditableCell extends Component {

	constructor(props) {
		super(props);
		this.state = {
			editing: false,
			isMouseOver: false,
		}
	}

	toggleEdit() {
		const editing = !this.state.editing;
		this.setState({ editing });
	}

	save() {
		const { user, handleSave,form } = this.props;
		form.validateFields((error, values) => {
			if (error) {
				return;
			}
			this.toggleEdit();
			handleSave({ ...user, ...values });
		});
	} 
s
	getPropName(type){
		if (type === 'bool') {
			return "checked";
		}
		return "value";
    }	
	getValue(type){
		const {
            dataIndex, user
        } = this.props;
		
		if (type === 'select' && this.props.options.length>0) {
			var opt = this.props.options.find(o=>o.value==user[dataIndex]);
			if(opt){
				return user[dataIndex];
				return ({
					key: user[dataIndex],
					label: opt.value,
				})
			}
		}
		return user[dataIndex];
    }
    renderCell = (form) => {
        const {
            editable, editing, dataIndex, user, type, children
        } = this.props;
        var content;
		
		form = form?form:{getFieldDecorator:()=>{}};
		const { getFieldDecorator } = form;
		
        if (editable) {
            if (editing) {
                content = 	<FormItem style={{ margin: 0 }}>
							{
                                getFieldDecorator(dataIndex, {
                                    rules: [],
                                    initialValue: user[dataIndex],
                                    valuePropName: "value"
                                })  (
                                    <GetInput type={type} options={this.props.options} typeImage={this.props.typeImage} objId={this.props.itemId}/>
					            )
						    }
							</FormItem>
            } else {
				content = 	<div
								className="editable-cell-value-wrap"
								style={{ paddingRight: 24 }}
							>
								{children}
							</div>
			}
        }
		if(!editable){
			content = 	<div
							className="editable-cell-value-wrap"
							style={{ paddingRight: 24 }}
						>
							{children}
						</div>
		}
        return (
            <td>
                {content}
            </td>
        );
    };
    render() {
        const {
            form
        } = this.props;
        return this.renderCell(form)
	}
}

export default EditableCell;
